import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";

import { v4 as uuid } from "uuid";
import { Input } from "../commons/Inputs";
import { ButtonSave } from "../commons/Button";
import {
   getBooksById,
  saveBooks,
} from "../../services/books/booksService";
import { Row, Col, Card, CardBody,  } from "reactstrap";
import {
  messageDialog,
  displayHttpErrorMessage,
} from "../../utils/utilityMessageDialogFunctions";
import { getInputValue } from "../../utils/utilityInputFunctions";
import {
  isValidPhone,
  hasEnoughLength,
} from "../../validations/generalValidations";
import useForm from "../../hooks/useForm";

const BookForm = () => {
  const initialState = {
    name: "",
    photo: "",
    phone: "",
  };
  const validations = {
    phone: (phone) =>
      !isValidPhone(phone) ? "Telefono especificado es invalido" : "",
    name: (name) =>
      !hasEnoughLength(name, 5) ? "Nombre especificado es invalido" : null,
  };

  const { handleChangeInput, errors, state, setState } =
    useForm(initialState, validations);

  const { name, phone } = state;
  const { id } = useParams();
  const history = useHistory();

  useEffect(() => populateBook(), []);

  const populateBook = async () => {
    const noFound = 404;
    try {
      if (id === "new") return;
      const {
        data
      } = await getBooksById(id);
      const book = data;

      if (book) {
        setState((data) => ({ ...data, ...book }));
      }
    } catch (error) {
      if (error?.response?.status === noFound) {
        history.replace("/not-found");
        console.error("erroer update");
      }
    }
  };

  const handleSaveBook = async () => {
    try {
      const { data } = await saveBooks(state);

      if (data.success) {
        history.replace("/books");
      }
      messageDialog({ text: data.message });
      return;
    } catch (error) {
      displayHttpErrorMessage(error);
    }
  };
  return (
    <center className="justify-content-center mt-5">
      <Card className={"w-25 align-items-center justify-content-center"}>
        <CardBody>
         
          <Row>
            <Col md={12}>
              <Input
                name="name"
                label="Nombre"
                value={name}
                onChange={handleChangeInput}
                errors={errors}
                isRequired
              />
            </Col>
            <Col md={12}>
              <Input
                name="phone"
                label="Teléfono"
                value={phone}
                onChange={handleChangeInput}
                errors={errors}
              />
            </Col>
          </Row>

          <Row>
            <Col md={12}>
              <div className="float-right mt-3">
                <ButtonSave onClick={handleSaveBook} />
              </div>
            </Col>
          </Row>
        </CardBody>
      </Card>
    </center>
  );
};

export default BookForm;









